* {
  margin: 0;
  padding: 0;
}

@import url("https://fonts.googleapis.com/css?family=Noto%20Sans%20Bamum:700|Noto%20Sans%20Bamum:400");

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Noto Sans Bamum";
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Noto Sans Bamum";
  font-weight: 700;
}

html {
  font-size: 100%;
} /* 16px */

h1 {
  font-size: var(--h1-font-size);
}

h2 {
  font-size: var(--h2-font-size);
}

h3 {
  font-size: var(--h3-font-size);
}

h4 {
  font-size: var(--h4-font-size);
}

h5 {
  font-size: var(--h5-font-size);
}

p,
a {
  font-size: var(--p-font-size);
}

small {
  font-size: var(--small-font-size);
}

.fill-height {
  height: 100%;
}

.min-screen-height {
  min-height: 100vh;
}

.fill-width {
  height: 100%;
}

.fill-container {
  width: 100%;
  height: 100%;
}

.flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-1 {
  flex: 1;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.text-center {
  text-align: center;
}
